        text_sections_Esculta_5:'Conciben os seus propios proxectos de cambio. Son eles e elas os verdadeiros protagonistas do seu futuro. Conciben a Empresa ou Proxecto desde a idea ata a execucin, coa mera supervisin do educador adulto. Responsabilzanse das sas accins e implsanse na sa propia motivacin.',
        text_sections_Esculta_6:'Empezan a exporse a sa formacin profesional e a fixarse retos. Fanse conscientes de que os estudos son o mis importante das sas vidas neste momento, sen deixar de lado tamn as sas aspiracins sociais e as sas ganas de divertirse de forma saudable. O coidado da sade cobra unha gran importancia nesta etapa para converterse nun adulto san.',
        
        text_sections_Clan_1:'A ltima etapa da educacin scout  o Clan Rover formado por mozos a partir de 17 anos.',
        text_sections_Clan_2:'Ser Rover  vivir intensamente cada paso, tomar as rendas e facer un proxecto da sa propia vida, empearse en cambiar o mundo e, sobre todo, crer que  posible.',
